<template>
  <div class="results">
    <div class="results-warp" v-if="isResults">
      <div class="results-header lowRisk" :class="resultsInfo.className">
        <div class="results-eval">
          <p class="results-eval-info">您现在属于中风</p>
          <p class="results-eval-end">{{resultsInfo.info}}</p>
        </div>
        <div class="results-chance">
          <div class="results-chance-data results-chance-stroke">
            <p class="results-chance-title" v-if="isFive">未来五年患中风的几率</p>
            <p class="results-chance-title" v-if="!isFive">未来十年患中风的几率</p>
            <p class="results-chance-num" v-if="isFive"><span>{{riskFive}}</span>%</p>
            <p class="results-chance-num" v-if="!isFive"><span>{{riskTen}}</span>%</p>
          </div>
          <div class="results-chance-data results-chance-multiple">
            <p class="results-chance-title">比同龄同性别健康者高出</p>
            <p class="results-chance-num" v-if="isFive"><span>{{rFive}}</span>倍</p>
            <p class="results-chance-num" v-if="!isFive"><span>{{rTen}}</span>倍</p>
          </div>
        </div>
      </div>
      <div class="gxs-cells-btn">
        <button type="button" v-if="isFive" @click="isFiveFn()" class="gxs-btn gxs-btn-default gxs-btn-block">点击查看10年风险结果</button>
        <button type="button" v-if="!isFive" @click="isFiveFn()" class="gxs-btn gxs-btn-default gxs-btn-block">点击查看5年风险结果</button>
      </div>
      <div class="gxs-cells-title">引发您患中风疾病的危险因素有：</div>
      <div class="gxs-flex">
        <router-link :to="{ name: 'resultsInfo', params: { id: item.index }}" class="gxs-flex-box" v-for="item in causeData">
          <button type="button">{{item.inputHint.title}}</button>
        </router-link>
      </div>
      <!--<div class="gxs-cells-btn">-->
        <!--<a href="http://api.forhoo.com/2017/20170622suanfufa/" type="button" class="gxs-btn gxs-btn-danger gxs-btn-block">开始答题</a>-->
      <!--</div>-->
      <div class="gxs-cells-btn">
        <router-link to="/" type="button" class="gxs-btn gxs-btn-zz gxs-btn-block">重新自测</router-link>
      </div>
    </div>
    <div class="results-warp" v-if="isResultsW">
      <div class="gxs-cells-title gxs-cells-title-hint">您还未进行卒中风险评估，<br>点击开始吧！</div>
      <div class="gxs-cells-btn">
        <router-link to="/" type="button" class="gxs-btn gxs-btn-default gxs-btn-block">开始自测</router-link>
      </div>
    </div>
  </div>
</template>


<script type='text/ecmascript-6'>
  import {mapState, mapMutations} from 'vuex'

  export default{
    data () {
      return {
        isFive: true,
        baseFive: null,
        baseTen: null,
        riskFive: null,
        riskTen: null,
        answerResultMap: null,
        rFive: 0,
        rTen: 0,
        resultsInfo: {},
        causeData: [],
        isResults: false,
        isResultsW: false
      }
    },
    computed: {
      ...mapState([
        'user', 'surveyData', 'codes', 'baseFive', 'baseTen', 'riskFive', 'riskTen', 'answerResultMap'
      ]),
      ...mapMutations([
        'getUser', 'getQuery'
      ])
    },
    methods: {
      cause () {
        console.log(this.$store.state.answerResultMap)
        for (let i = 0; i < this.$store.state.answerResultMap.isSHowAry.length; i++) {
          if (this.surveyData.pages[i].inputHint !== undefined && this.$store.state.answerResultMap.isSHowAry[i] === true && this.$store.state.answerResultMap.isSHowAry[i] !== null) {
            this.causeData.push(this.surveyData.pages[i])
          }
        }
      },
      isFiveFn () {
        this.isFive = !this.isFive
      },
      background () {
        let num
        if (this.isFive) {
          num = this.rFive
        } else {
          num = this.rTen
        }
        if (num <= 3.5 && num >= 0) {
          this.resultsInfo.className = 'lowRisk'
          this.resultsInfo.info = '低危人群'
        } else if (num <= 7 && num >= 3.6) {
          this.resultsInfo.className = 'crisis'
          this.resultsInfo.info = '中危人群'
        } else if (num <= 10 && num >= 7.1) {
          this.resultsInfo.className = 'highRisk'
          this.resultsInfo.info = '高危人群'
        } else if (num > 10) {
          this.resultsInfo.className = 'VeryHighRisk'
          this.resultsInfo.info = '极高危人群'
        }
      },
      getRelapseInfo () {
        this.isResults = true
        this.isResultsW = false
        this.riskFive = this.$store.state.riskFive
        this.riskTen = this.$store.state.riskTen
        this.rFive = parseInt(this.$store.state.riskFive / this.$store.state.baseFive)
        this.rTen = parseInt(this.$store.state.riskTen / this.$store.state.baseTen)
        this.cause()
        this.background()
      }
    },
    mounted () {
      console.log(this.$store.state.answerResultMap)
      if (this.user === false) {
        this.isResultsW = true
      } else {
        this.getRelapseInfo()
      }
    }
  }
</script>





